<script>
import { defineComponent } from '@vue/composition-api';
import { rendererProps, useJsonFormsControl } from '@jsonforms/vue2';
import RadioButtons from '~/webapps-common/ui/components/forms/RadioButtons';
import { optionsMapper } from '@/utils/nodeDialogUtils';
import LabeledInput from './LabeledInput';

const RadioInput = defineComponent({
    name: 'RadioInput',
    components: {
        RadioButtons,
        LabeledInput
    },
    props: {
        ...rendererProps()
    },
    setup(props) {
        return useJsonFormsControl(props);
    },
    data() {
        return {
            options: null
        };
    },
    mounted() {
        this.options = this.control?.schema?.oneOf?.map(optionsMapper);
    },
    methods: {
        onChange(event) {
            this.handleChange(this.control.path, event);
        }
    }
});
export default RadioInput;
</script>

<template>
  <LabeledInput
    v-if="control.visible"
    :text="control.label"
    :scope="control.uischema.scope"
  >
    <RadioButtons
      v-if="options"
      :possible-values="options"
      :disabled="!control.enabled"
      :value="control.data"
      @input="onChange"
    />
  </LabeledInput>
</template>

<style lang="postcss" scoped>
.labeled-input {
  margin-bottom: 10px;
}
</style>
