<script>
import Label from '~/webapps-common/ui/components/forms/Label';
import ErrorMessage from './ErrorMessage';
import ReexecutionIcon from '~/webapps-common/ui/assets/img/icons/reexecution.svg?inline';

const LabeledInput = {
    name: 'LabeledInput',
    components: {
        Label,
        ErrorMessage,
        ReexecutionIcon
    },
    props: {
        text: {
            default: '',
            type: String
        },
        errors: {
            default: () => [],
            type: Array
        },
        showErrors: {
            default: true,
            type: Boolean
        },
        scope: {
            default: '',
            type: String
        }
    },
    computed: {
        isModelSetting() {
            return this.scope.includes('model');
        }
    }
};
export default LabeledInput;
</script>

<template>
  <div class="labeled-input">
    <Label
      :text="text"
      :compact="true"
    >
      <ReexecutionIcon
        v-if="isModelSetting"
        class="reexecution-icon"
      />
      <slot />
    </Label>
    <ErrorMessage
      v-if="showErrors"
      :error="errors"
    />
  </div>
</template>

