<script>
import { JsonDataService } from 'knime-ui-extension-service';
import { vanillaRenderers } from '@jsonforms/vue2-vanilla';
import { JsonForms } from '@jsonforms/vue2';
import Button from '~/webapps-common/ui/components/Button';
import { fallbackRenderers, defaultRenderers } from '@/components/renderers';
import { createAjv } from '@jsonforms/core';

const renderers = [...vanillaRenderers, ...fallbackRenderers, ...defaultRenderers];

export default {
    components: {
        JsonForms,
        Button
    },
    inject: ['knimeService'],
    data() {
        return {
            jsonDataService: null,
            settings: null,
            renderers: Object.freeze(renderers)
        };
    },
    async mounted() {
        this.jsonDataService = new JsonDataService(this.knimeService);
        this.settings = await this.jsonDataService.initialData();
        this.jsonDataService.registerDataGetter(this.getData);
        // TODO UIEXT-254: This is needed until we update to jsonforms 3.0.x as otherwise the different number types are
        // not supported
        this.ajv = createAjv({ unknownFormats: 'ignore' });
    },
    methods: {
        getData() {
            return this.settings.data;
        },
        onSettingsChanged(data) {
            if (data.data) {
                this.settings.data = data.data;
                this.jsonDataService.publishData(this.settings);
            }
        },
        async applySettings() {
            await this.jsonDataService.applyData();
            this.closeDialog();
        },
        closeDialog() {
            window.closeCEFWindow();
        }
    }
};
</script>

<template>
  <div class="dialog">
    <div class="form">
      <JsonForms
        v-if="settings"
        :data="settings.data"
        :schema="settings.schema"
        :uischema="settings.ui_schema"
        :renderers="renderers"
        :ajv="ajv"
        @change="onSettingsChanged"
      />
    </div>
    <div class="controls">
      <Button
        with-border
        compact
        @click="closeDialog"
      >
        Cancel
      </Button>
      <Button
        primary
        compact
        @click.prevent="applySettings"
      >
        Ok
      </Button>
    </div>
  </div>
</template>

