<script>
import { defineComponent } from '@vue/composition-api';
import { optionsMapper } from '@/utils/nodeDialogUtils';
import DropdownInput from './DropdownInput';

const ColumnSelect = defineComponent({
    name: 'ColumnSelect',
    components: {
        DropdownInput
    },
    inheritAttrs: false,
    methods: {
        optionsGenerator(control) {
            return [
                ...control.uischema.options.showNoneColumn ? [{ id: '<none>', text: 'None' }] : [],
                ...control.uischema.options.showRowKeys ? [{ id: '<row-keys>', text: 'Row keys' }] : [],
                ...control.schema.oneOf.map(optionsMapper)
            ];
        }
    }
});
export default ColumnSelect;
</script>

<template>
  <DropdownInput
    v-bind="$attrs"
    :options-generator="optionsGenerator"
  />
</template>
