<script>
import { defineComponent } from '@vue/composition-api';
import { rendererProps, useJsonFormsControl } from '@jsonforms/vue2';
import Dropdown from '~/webapps-common/ui/components/forms/Dropdown';
import { optionsMapper } from '@/utils/nodeDialogUtils';
import LabeledInput from './LabeledInput';

const DropdownInput = defineComponent({
    name: 'DropdownInput',
    components: {
        Dropdown,
        LabeledInput
    },
    props: {
        ...rendererProps(),
        optionsGenerator: {
            type: Function,
            required: false,
            default: (control) => control.schema.oneOf.map(optionsMapper)
        }
    },
    setup(props) {
        return useJsonFormsControl(props);
    },
    data() {
        return {
            options: null
        };
    },
    mounted() {
        this.options = this.optionsGenerator(this.control);
    },
    methods: {
        onChange(event) {
            this.handleChange(this.control.path, event);
        }
    }
});
export default DropdownInput;
</script>

<template>
  <LabeledInput
    :text="control.label"
    :scope="control.uischema.scope"
  >
    <Dropdown
      v-if="options"
      :aria-label="control.label"
      :disabled="!control.enabled"
      :value="control.data"
      :possible-values="options"
      @input="onChange"
    />
  </LabeledInput>
</template>
