<script>
import { defineComponent } from '@vue/composition-api';
import { rendererProps, useJsonFormsControl } from '@jsonforms/vue2';
import NumberInput from '~/webapps-common/ui/components/forms/NumberInput';
import LabeledInput from './LabeledInput';

const NumberInputBase = defineComponent({
    name: 'NumberInputBase',
    components: {
        NumberInput,
        LabeledInput
    },
    props: {
        ...rendererProps(),
        type: {
            type: String,
            required: false,
            default: 'double'
        }
    },
    setup(props) {
        return useJsonFormsControl(props);
    },
    methods: {
        onChange(event) {
            this.handleChange(this.control.path, event);
        }
    }
});
export default NumberInputBase;
</script>

<template>
  <LabeledInput
    v-if="control.visible"
    :text="control.label"
    :errors="[control.errors]"
    :scope="control.uischema.scope"
  >
    <NumberInput
      :disabled="!control.enabled"
      :value="control.data"
      :type="type"
      :min="control.schema.minimum"
      :max="control.schema.maximum"
      @input="onChange"
    />
  </LabeledInput>
</template>
